<template>
	<div id="memberBind">
		<public-header title="更改绑定手机号"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field v-model="mobile" label="手机号" placeholder="请输入手机号"/>
				<template v-if="isverifymobile == 1">
					<van-field v-model="captcha" type="number" label="图形验证码" placeholder="请输入图形验证码"/>
					<van-field v-model="yzm" label="短信验证码" placeholder="请输入短信验证码" >
						<van-button slot="button" size="small" type="primary" @click="onGetCode()" v-if="code.downcount == 60">{{code.text}}</van-button>
						<van-button slot="button" size="small" type="primary" disabled v-else>{{code.text}}</van-button>
					</van-field>
				</template>
				<van-field v-model="password" type="password" label="登录密码" placeholder="请输入您的登录密码"/>
				<van-field v-model="repassword" type="password" label="确认密码" placeholder="请输入确认登录密码"/>
			</van-cell-group>
			<template v-if="isverifymobile == 1">
				<div class="email-img" @click="onRefreshCaptcha">
					<img :src="captcha_img" alt="">
				</div>
			</template>
			<div class="submit">
				<van-button size="large" type="danger" @click="onSubmit()">立即绑定</van-button>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				preLoading: true,
				mobile: '',
				isverifymobile: 0,
				yzm: '',
				password: '',
				repassword: '',
				getCode: true,
				code: {text : '获取验证码', downcount: 60},
				captcha: '',
				captcha_img: ''
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				that.util.request({
					url: 'wmall/member/profile/info',
				}).then((res) => {
					that.preLoading = false;
					var result = res.data.message;
					if(result.errno) {
						that.$toast(result.message);
						return false;
					}
					that.mobile = result.message.mobile;
					that.isverifymobile = result.message.isverifymobile;
				});
				that.onRefreshCaptcha();
			},
			onGetCode() {
				if(!this.getCode) {
					return false;
				}
				if(!this.mobile) {
					this.$toast('手机号不能为空');
					return false;
				}
				if(!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(!this.captcha) {
					this.$toast('请输入图形验证码');
					return false;
				}
				this.util.request({
					url: 'system/common/code',
					method: 'POST',
					data: {
						mobile: this.mobile,
						captcha: this.captcha
					},
				}).then((res) => {
					var result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					} else {
						this.code.text = this.code.downcount + "秒后重新获取";
						var timer = setInterval(() => {
							this.code.downcount--;
							if(this.code.downcount <= 0){
								clearInterval(timer);
								this.code.text = "获取验证码";
								this.code.downcount = 60;
								this.getCode = true;
							} else {
								this.code.text = this.code.downcount + "秒后重新获取";
							}
						}, 1000);
						this.$toast('验证码发送成功, 请注意查收');
					}
				})
			},
			onSubmit() {
				if(!this.mobile){
					this.$toast('手机号不能为空');
					return false;
				}
				if(!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(this.isverifymobile == 1) {
					if(!this.yzm) {
						this.$toast('请输入短信验证码');
						return false;
					}
				}
				if (!this.password) {
					this.$toast('密码不能为空');
					return false;
				} else {
					var length = this.password.length;
					if(length < 8 || length > 20) {
						this.$toast("请输入8-20位密码");
						return false;
					}
					var reg = /[0-9]+[a-zA-Z]+[0-9a-zA-Z]*|[a-zA-Z]+[0-9]+[0-9a-zA-Z]*/;
					if(!reg.test(this.password)) {
						this.$toast("密码必须由数字和字母组合");
						return false;
					}
				}
				if (!this.repassword) {
					this.$toast('请重复输入密码');
					return false;
				}
				if(this.password != this.repassword) {
					this.$toast('两次密码输入不一致');
					return false;
				}
				var params = {
					mobile: this.mobile,
					code: this.yzm,
					password: this.password,
					repassword: this.repassword,
					force: 0,
				}
				this.util.request({
					url: 'wmall/member/profile/bind',
					data: params,
				}).then((res) => {
					var result = res.data.message;
					if(result.errno) {
						if(result.errno != -2) {
							this.util.$toast(result.message);
							return false;
						} else {
							this.$dialog.confirm({
								message: '该手机号已绑定其他账号，确定进行合并吗',
								confirmButtonColor: '#ff2d4b',
								confirmButtonText: 'جەزىملەش',
								cancelButtonText: 'قالدۇرۇش'
							}).then((res) => {
								params.force = 1;
								this.util.request({
									url: 'wmall/member/profile/bind',
									data: params,
								}).then((res) => {
									var result = res.data.message;
									if(result.errno) {
										this.util.$toast(result.message);
										return false;
									} else {
										this.util.$toast(result.message, this.util.getUrl({path: '/pages/member/profile'}), 1500, 'replace')
									}
								})
							}).catch((res) => {
								console.log('cancel')
							})
						}
					} else {
						this.util.$toast(result.message, this.util.getUrl({path: '/pages/member/profile'}), 1500, 'replace')
					}
				})
			},
			onRefreshCaptcha() {
				this.util.request({
					url: 'wmall/member/profile/captcha',
				}).then((res) => {
					this.captcha_img = res.data.message.message.captcha;
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#memberBind .content{
		padding-top: 10px;
	}
	#memberBind .content .submit{
		margin-top: 20px;
		padding: 0 15px;
	}
	#memberBind .email-img{
		height: 32px;
		width: 90px;
		position: absolute;
		top: 59px;
		left: 15px;
	}
	#memberBind .email-img img{
		border: none;
		width: 100%;
		height: 100%;
	}
</style>



